.button {
  background-color: #4CAF50; /* Green */
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
}

.rowC {
  display:flex; 
  flex-direction:row;
  padding-left: 80px;
}

.center {
  text-align: center;
  border: 3px solid black;
}

.up {
  padding-top: 80px;
}

body {
  background-color: #FAF79B;
  font-family: "Courier", "monospace";
  text-align: center;
}
.color{
  color: #416a59;
}

.size{
  font-size: 20px;
}
h1 {
  text-decoration: underline;
}

h2{
  font-size: 30px;
}

.center-screen {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  min-height: 100vh;
}

.parent{
  border: 1px solid black;
  margin: 1rem;
  padding: 2rem 2rem;
  text-align: center;
}

.child{
  display: inline-block;
  padding: 1rem 1rem;
  vertical-align: middle;
}
.positioning_right {
  position: absolute;
  right: 0%;
  bottom: 40%;
}

.positioning_left {
  position: absolute;
  left: 0%;
  bottom: 40%;
} 
.spacing{
  padding-bottom: 5px;
}